.border {
  list-style: none;
  padding: 0;
}

.border li {
  font-family: 'Trebuchet MS', 'Lucida Sans';
  padding: 7px 20px;
  margin-bottom: 10px;
  border-radius: 5px;
  border-left: 10px solid rgb(76, 175, 80);
  box-shadow: 2px -2px 5px 0 rgba(0, 0, 0, 0.1),
    -2px -2px 5px 0 rgba(0, 0, 0, 0.1), 2px 2px 5px 0 rgba(0, 0, 0, 0.1),
    -2px 2px 5px 0 rgba(0, 0, 0, 0.1);
  font-size: 20px;
  letter-spacing: 2px;
  transition: 0.3s all linear;
}

.border li:nth-child(2) {
  border-color: rgb(191, 191, 191);
}

.border li:nth-child(3) {
  border-color: rgb(251, 72, 72);
}

.border li:nth-child(4) {
  border-color: rgb(17, 198, 192);
}

.border li:nth-child(5) {
  border-color: rgb(97, 65, 46);
}
